<template>
  <v-container>
    <!-- <v-col cols="12" class="text-left orders__back" @click.prevent="goBack">Regresar</v-col> -->

    <h1 class="profile">Mis órdenes</h1>
    <h5>Consulta el detalle de tus últimas compras</h5>

    <v-col v-if="!myOrders.length">
      <v-card>
        <v-card-title class="orders__orders-empty">
          <v-icon color="primary" large left>mdi-calendar-alert</v-icon>
          <span>No has hecho ninguna orden</span>
        </v-card-title>
      </v-card>
    </v-col>

    <v-col cols="12" v-for="order in myOrders" :key="order.id">
      <v-card
        class="mx-auto"
        @click.prevent="goToDetails(order.id, order)
          "
      >
        <v-row>
          <v-col class="pa-1">
            <v-card-title class="py-1 orders__orders-text">
              <v-icon large small left color="primary">mdi-calendar-month</v-icon>
              <span>Orden #{{ order.id }}</span>
            </v-card-title>
            <v-card-text class="py-1">{{deliveryType(order.delivery_type_id)}}</v-card-text>
            <v-card-text class="py-1">Fecha y Hora: {{order.created_at}}</v-card-text>
          </v-col>
          <v-col cols="auto" class="pa-5 my-auto">
            <v-icon color="primary">mdi-chevron-right</v-icon>
          </v-col>
        </v-row>
      </v-card>
    </v-col>
  </v-container>
</template>

<script>
import { mapState, mapActions } from "vuex";
export default {
  data() {
    return {};
  },
  async mounted() {
    await this.getUserOrders();
  },
  computed: {
    ...mapState("orders", ["myOrders"])
  },
  methods: {
    ...mapActions("orders", ["getUserOrders", "setOrderData"]),
    deliveryType(id) {
      if (id === 1) {
        return "Entrega a domicilio";
      } else {
        return "Recoge en tienda";
      }
    },
    goBack() {
      this.$router.go(-1);
    },
    goToDetails(id, data) {
      this.setOrderData(data)
      this.$router.push({ name: "orderDetail", params: { id } }).catch(err => err);
    }
  }
};
</script>
<style lang="scss" scoped>
.orders {
  &__back {
    font-size: 16px;
    font-weight: 700;
    color: var(--color-primary);
    cursor: pointer;
  }
  &__orders {
    &-empty {
      padding: 1rem;
      font-size: 1rem;
      font-family: var(--font-family);
      color: var(--color-primary);
      font-weight: 600;
    }
    &-text {
      padding: 1rem;
      font-size: 1rem;
      font-family: var(--font-family);
      color: var(--color-primary);
      font-weight: 600;
    }
  }
}
</style>